/*--header--*/
header {
  background: #F6F3E8;
  .navbar-nav a {
    height: 100%;
    line-height: normal;
  }
  .navbar {
    min-height: 0;
    height: 68px;
    background: #FFFFFF;
    border-bottom: none;
    border-radius: 0;
    .large-container {
      display: flex;
      align-items: center;
    }
  }
  .navbar-right {
    margin-right: 0;
    li {
      height: 100%;
    }
  }
  .top-navbar {
    background-color: #f6f3e8;
    border-bottom: 1px solid #bdbcb3;
    height: 48px;
    &>container {
      height: 100%;
    }
    .navbar-nav {
      height: 100%;
      li {
        display: flex;
        align-items: center;
      }

    }
  }
  .logo {
    float: left;
  }
  .navbar-collapse {
    padding-right: 0;
    padding-left: 0;
  }
  .large-container {
    height: 100%;
  }
  .tel-num {
    font-size: 21px;
    color: rgb(65, 64, 62);
    font-family: 'HelveticaNeueCyr-Roman', 'Roboto', sans-serif;
    padding-right: 30px;
    border-right: 1px solid #BDBCB3;
  }
  .glyphicon-earphone {
    top: 5px;
  }
  .callback-trigger {
    color: rgb(65, 64, 62);
    font-size: 15px;
    font-family: 'HelveticaNeueCyr-Roman', 'Roboto', sans-serif;
    display: flex !important;
    height: auto !important;
    padding: 0 30px;
    border-right: 1px solid #BDBCB3;
    & > span {
      border-bottom: 1px dashed #444444;
      transition: all 0.2s ease;
    }
    &:hover {
      &>span {
        border-bottom: 1px dashed transparent;
      }

    }
  }
  #select-city {
    color: rgb(65, 64, 62);
    font-size: 15px;
    font-family: 'HelveticaNeueCyr-Roman', 'Roboto', sans-serif;
    position: relative;
    &:after {
      content: "\e114";
      font-family: 'Glyphicons Halflings';
      color: #F34445;
      font-size: 13px;
      margin-left: 3px;
      position: relative;
      top: 1px;
    }
  }
  .country:after {
    content: "|";
    margin: 0 2px;
  }
  .my-notebook {
    padding: 0 !important;
    display: flex !important;
    align-items: center;
    color: rgb(68, 68, 68);
    font-size: 15px;
    text-transform: uppercase;
    border-right: 1px solid #BDBCB3;
    .red-but {
      width: 53px;
      background: #EC4445;
      color: #FEF6F6;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 18px;
    }
    .inscription {
      padding: 0 15px;
      font-weight: bold;
    }
  }
  .logo {
    width: 240px;
    & > img {
      display: block;
      width: 100%;
      height: auto;
    }
  }
  .navbar-collapse.collapse {
    height: 100% !important;
    display: flex !important;
    align-items: center;
  }
  #main-menu {
    font-size: 16px;
    font-family: 'HelveticaNeueCyr-Roman', 'Roboto', sans-serif;
    a {
      color: #444;
      text-decoration: underline;
      margin: 0 5px;
      &:hover {
        background: transparent;
      }
    }
    .navbar-nav {
      margin:  0 25px;
    }
  }
  .city-select-cont {
    background: #E3E0D5;
    min-height: 100px;
    .top-panel {
      border-bottom: 1px solid #BDBCB3;
      padding: 10px 0;
    }
    .top-panel-in {
      height: 48px;
      display: flex;
      justify-content: space-between;
      a {
        color: #41403e;
        display: flex;
        align-items: center;
        padding: 0 20px;
      }
    }
    .set-country {
      text-transform: uppercase;
      border-right: 1px solid #BDBCB3;

    }
  }

}